import React from 'react';
import { changeLog } from '../changelog';

const ordered = changeLog.sort( (a, b) => b.id - a.id );

export const AboutScreen = () => {

    return (
        <div className="mt2">
            <h1>About Section</h1>
            <hr />

            <p>React-MultiApp is a Simple React app with multiple utilities made for educational purposes.</p>
            <br />
            <p>It includes <strong>ToDo list</strong>, <strong>Quotes</strong>, <strong>Gif Searching</strong> and some other functionalities all in one, done to exercise React and JavaScript skills.</p>
            <br />
            <p>
                You can take a look if you want on <a
                                                        target="_blank"
                                                        rel="noopener noreferrer"
                                                        href="https://github.com/init1-dev/React-MultiApp">
                                                            GitHub repository
                                                    </a>
            </p>
            <br />
            <h4><strong>Changelog:</strong></h4>
            {
                ordered.map( log => {
                    return (
                        <div className="log" key={log.id}>
                            <p><strong>{ log.date }</strong>: <i>{ log.desc }</i></p>
                        </div>
                    )
                }) 
            }
        </div>
    )
};